<template>
  <section class="collapsible" v-bind:data-collapsed="collapsed" :style="{ 'background-color': backgroundColor }">
    <h2 :class="'section-name' + ' ' + extraClass" v-on:click="onClick" v-html="'<span class=\'arrow\'></span> ' + name"></h2>
    <section class="contents"><slot /></section>
  </section>
</template>

<script>
export default {
  name: "CollapsiblePane",
  props: {
    name: String,
    extraClass: {
      type: String,
      default: '',
    },
    backgroundColor: {
      type: String,
      default: "cornflowerblue",
    },
    startCollapsed: {
      type: Boolean,
      default: true,
      required: false,
    }
  },
  data: () => {
    return {
      collapsed: true,
    }
  },
  created() {
    this.collapsed = this.startCollapsed;
  },
  methods: {
    onClick() {
      this.collapsed = !this.collapsed;
    }
  },
}
</script>

<script setup>

</script>

