<template>
  <MainPage :lesson-num="lessonNum" :lesson-name="lessonName">
    <CollapsiblePane name="Introduction" extra-class="section" :start-collapsed="startCollapsed">
      <div class="inner-text lesson-intro">
        <p v-html="intro"></p>
      </div>
    </CollapsiblePane>
    <LessonSection
      v-for="(section, index) in sections"
      :key="index"
      :right-col-width="section.rightColWidth"
      :start-collapsed="startCollapsed"
      :lessonNum="lessonNum"
      :sectionNum="index + 1"
      :sectionName="section.name"
      :web="section.web"
      :book="section.book"
      :exam="section.exam"
      :intro="section.intro"
      :general="section.general"
      :specific="section.specific"
      :steps="section.steps"
      :examples="section.examples"
    />
  </MainPage>
</template>

<script>
import MainPage from '@/components/MainPage'
import LessonSection from "@/components/LessonSection";
import CollapsiblePane from "@/components/CollapsiblePane";

export default {
  name: 'App',
  components: {
    CollapsiblePane,
    LessonSection,
    MainPage,
  },
  mounted() {
    // Replace \p
    (() => {
      const matchingElementSet = document.evaluate("//*[text()[contains(.,'\\p')]]", document, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
      // const matchingElementSet = document.evaluate("//*[contains(text(),'\\p')]", document, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
      const elements = [];

      let element;
      // eslint-disable-next-line no-cond-assign
      while(element = matchingElementSet.iterateNext()) {
        console.log(element);
        elements.push(element);
      }

      for(const element of elements) {
        const parts = element.innerHTML.split('\\p');
        let html = parts[0];

        for(let i = 1; i < parts.length; i++) {
          html += '<span class="invisible">' + parts[i] + '</span>';
        }

        element.innerHTML = html;
      }
    })();

    window.addEventListener('keyup', event => {
      if(event.key === 'v') {
        for(const e of document.querySelectorAll('.invisible')) {
          if(e.style.visibility === 'hidden') {
            e.style.visibility = 'visible';
            break;
          }
        }
      }
    });
  },
  data() {
    function replaceStuff(obj, changes) {
      for(const key in obj) {
        if(typeof obj[key] === 'object') {
          obj[key] = replaceStuff(obj[key], changes);
        } else if(typeof obj[key] === 'string') {
          for(const from in changes) {
            obj[key] = obj[key].replaceAll(from, changes[from]);
          }
        }
      }

      return obj;
    }

    return replaceStuff(window.j, {
      '<pli>': '<li class="invisible">',
      '</pli>': '</li>'
    });
  }
}
</script>

